body {
  font-family: sans-serif;
  margin: 0px;
}

#canvas {
  border: 0px solid black !important;
  display: block;
}

button {
  text-align: center !important;
  width: 80px;
  height: 28px;
  border: 0px;
  margin: 0px !important;
  border: solid 1px black;
}

#modebutton,
#backgroundbutton {
  background-color: #ccc;
}

#colorbutton {
  background-color: #000;
  color: white;
  border: solid 1px white;
}
